      sh: [{ tit: '上海迪士尼乐园', logo: __webpack_require__("UCzs"), text: '为什么一定要去上海迪士尼乐园亮点1 ：国内首座迪士尼主题乐园，家门口的世界级家庭娱乐目的地。亮点2 ：拥有全球最大的迪士尼城堡、独有的海岛主题园区、最长的迪士尼花车巡游路线等。亮点3 ：探索别具一格的六大主题园区—米奇大街、奇想花园、梦幻世界、探险岛、宝藏湾和明日世界。' }, { tit: '上海东方明珠', logo: __webpack_require__("9hGm"), text: '上海东方明珠广播电视塔座落于上海黄浦江畔、浦东陆家嘴嘴尖，以其468米的绝对高度成为亚洲第一、世界第三之高塔。' }, { tit: '上海外滩', logo: __webpack_require__("//X7"), text: '外滩（The Bund），位于上海市中心区的黄浦江畔，它是上海的风景线，是到上海观光的游客必到之地。外滩又名中山东一路，全长约1.5公里。东临黄浦江，西面为哥特式、罗马式、巴洛克式、中西合壁式等52幢风格各异的大楼，被称为“万国建筑博览群”。' }, { tit: '上海迪士尼乐园', logo: __webpack_require__("UCzs"), text: '为什么一定要去上海迪士尼乐园亮点1 ：国内首座迪士尼主题乐园，家门口的世界级家庭娱乐目的地。亮点2 ：拥有全球最大的迪士尼城堡、独有的海岛主题园区、最长的迪士尼花车巡游路线等。亮点3 ：探索别具一格的六大主题园区—米奇大街、奇想花园、梦幻世界、探险岛、宝藏湾和明日世界。' }, { tit: '上海东方明珠', logo: __webpack_require__("9hGm"), text: '上海东方明珠广播电视塔座落于上海黄浦江畔、浦东陆家嘴嘴尖，以其468米的绝对高度成为亚洲第一、世界第三之高塔。' }, { tit: '上海外滩', logo: __webpack_require__("//X7"), text: '外滩（The Bund），位于上海市中心区的黄浦江畔，它是上海的风景线，是到上海观光的游客必到之地。外滩又名中山东一路，全长约1.5公里。东临黄浦江，西面为哥特式、罗马式、巴洛克式、中西合壁式等52幢风格各异的大楼，被称为“万国建筑博览群”。' }],
